import React from "react"
import {Link, graphql} from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {rhythm} from "../utils/typography"
import { useForm, useFormScreenPlugin } from "tinacms"

const BlogIndex = ({data, location}) => {
    const siteTitle = data.site.siteMetadata.title
    const posts = data.allMarkdownRemark.edges

    const [styles, stylesForm] = useForm({
        id: "blog-index-styles",
        label: "Blog Styles",
        initialValues: {
            backgroundColor: "",
            hideBio: false,
            date: "2019 03 04",
            thumbnail:
                "https://images.unsplash.com/photo-1518259102261-b40117eabbc9?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2550&q=80",
        },
        fields: [
            {
                name: "backgroundColor",
                label: "Heading Color",
                component: "color",
                colorFormat: "HeX",
            },
            {
                name: "hideBio",
                label: "Hide Bio",
                component: "toggle",
            },
            {
                name: "date",
                label: "Date",
                component: "date",
                dateFormat: "YYYY MM DD",
                timeFormat: null,
            },
        ],
        onSubmit() {
            alert("Saving doesn't do anything.")
        },
    })

    useFormScreenPlugin(stylesForm)

    return (
        <Layout location={location} title={siteTitle}>
            <SEO title="All posts"/>
            <Bio/>
            {posts.map(({node}) => {
                const title = node.frontmatter.title || node.fields.slug
                return (
                    <article key={node.fields.slug}>
                        <header>
                            <h3
                                style={{
                                    marginBottom: rhythm(1 / 4),
                                }}
                            >
                                <Link style={{boxShadow: `none`}} to={node.fields.slug}>
                                    {title}
                                </Link>
                            </h3>
                            <small>{node.frontmatter.date}</small>
                        </header>
                        <section>
                            <p
                                dangerouslySetInnerHTML={{
                                    __html: node.frontmatter.description || node.excerpt,
                                }}
                            />
                        </section>
                    </article>
                )
            })}
        </Layout>
    )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`
